<template>
  <div id="home" class="home">
    <Navbar class="home-nav">
      <template #center>
        <div class="title">购物街</div>
      </template>
    </Navbar>
    <TabControl
            :title="title"
            @tabClick='tabClick'
            ref="tabControl1"
            v-show="isFixed"
            class="tab-control"/>
    <scroll class="content" ref="scroll"
            :probe-type="3"
            :pull-up-load="true"
            @pullingUp="loadMore"
            @scroll="scrollContent">
      <Homeswipper
              :banner="banner"
              @swiperImageLoad="swiperImageLoad"
              class="banner"/>
      <RecommendView :recommends="recommends"/>
      <FeatureView/>
      <TabControl
              :title="title"
              @tabClick='tabClick'
              ref="tabControl2"
              class="tab-control"/>
      <GoodsList :goods="goods[currentType].list"></GoodsList>
    </scroll>
    <!--    添加.native属性可以使用原生的点击-->
    <backTop @click.native="backClick" v-show="isShowBackTop"></backTop>
  </div>

</template>

<script>
//公共组件
import Navbar from "@components/common/navbar/Navbar";
import TabControl from "@components/content/tabControl/TabControl";
import Scroll from "@components/common/scroll/Scroll";
import backTop from "@components/content/backTop/backTop";

//子组件
import Homeswipper from "./childComps/Homeswipper";
import RecommendView from "./childComps/RecommendView";
import FeatureView from "./childComps/FeatureView";
import GoodsList from "@components/content/goods/GoodsList";

//方法
import {getHomeMultidata, getHomeGoods} from "@network/home";
import {debounce} from '@common/utils'
import {itemListenerMixin} from '@common/mixin'

export default {
  name: "Home",
  components: {
    Navbar,
    Homeswipper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
    Scroll,
    backTop
  },
  mixins: [itemListenerMixin],
  data() {
    return {
      result: [],
      recommends: [],
      banner: [],
      title: ['流行', '新款', '精选'],
      goods: {
        'pop': {page: 0, list: []},
        'new': {page: 0, list: []},
        'sell': {page: 0, list: []}
      },
      currentType: 'pop',
      isShowBackTop: false,
      tabOffsetTop: 0,
      isFixed: false,
      saveY: 0,
      itemImgListener: null
    }
  },
  created() {
    // 1.请求多个数据
    this.getHomeMultidata();
    //goods
    this.getHomeGoods('pop');
    this.getHomeGoods('new');
    this.getHomeGoods('sell');

  },
  mounted() {
    // //监听图片加载完成
    // let refresh = debounce(this.$refs.scroll.refresh, 500);
    // //对事件监听的保存
    // this.itemImgListener=()=>{
    //   refresh();
    // };
    // this.$bus.$on('ItemLoaded', this.itemImgListener)

  },
  destroyed() {

  },
  activated() {
    this.$refs.scroll.scrollTo(0, this.saveY, 0);
    this.$refs.scroll.refresh();
  },
  deactivated() {
    //保存Y值
    this.saveY = this.$refs.scroll.getScrollY();
    //取消全局事件监听
    this.$bus.$off('ItemLoaded', this.itemImgListener);
  },
  methods: {
    //事件相关
    tabClick(index) {
      //Es6方法
      let type = Object.keys(this.goods);
      this.currentType = type[index];
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;
    },
    backClick() {
      // console.log(this.$refs.scroll.scroll);
      //通过组件调用封装的scrollTo 方法
      this.$refs.scroll.scrollTo(0, 0)
    },
    //监听首页滚动
    scrollContent(position) {
      //判断backToTop是否显示
      this.isShowBackTop = -position.y > 1000;
      //tabControl是否吸顶
      this.isFixed = -position.y > this.tabOffsetTop
    },
    //加载更多
    loadMore() {
      this.getHomeGoods(this.currentType);
      //重新计算新的可滚动高度
      // this.$refs.scroll.scroll.refresh();
    },
    //获取tabcontrol的offsetTop
    //每一个组件内都有一个$el,当轮播图图片全部加载完毕之后获取
    swiperImageLoad() {
      console.log(this.$refs.tabControl2.$el.offsetTop)
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
    },

    //网络请求相关
    getHomeMultidata() {
      getHomeMultidata().then(res => {
        this.result = res;
        this.recommends = res.data.data.recommend.list;
        this.banner = res.data.data.banner.list;
      })
    },
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then(res => {
        this.goods[type].list.push(...res.data.data.list);
        this.goods[type].page += 1;
        //完成下拉更多
        this.$refs.scroll.finishPullUp();
      });
    },

  }
}
</script>
<style scoped>

  .home {
    height: 100vh;
    position: relative;
    background-color: #fff;
  }

  .banner {
    width: 100%;
    overflow: hidden;
  }

  .home-nav {
    background-color: var(--color-tint);
    color: #fff;
    /*由于使用bscroll进行滚动，下方元素一直在指定区域进行滚动，上方导航不需脱离文档流*/
    /*position: fixed;*/
    /*top: 0;*/
    width: 100%;
    /*z-index: 99;*/
  }

  /*.tab-control {*/
  /*  position: sticky;*/
  /*  top: 44px;*/
  /*}*/

  /*.content {*/
  /*  position: absolute;*/
  /*  top: 44px;*/
  /*  bottom: 49px;*/
  /*  left: 0;*/
  /*  right: 0;*/
  /*  overflow: hidden;*/
  /*}*/
  .content {
    height: calc(100% - 49px);
    overflow: hidden;
  }

  .tab-control {
    position: relative;
    z-index: 9;
  }

  /*.fixed{*/
  /*  position: fixed;*/
  /*  top:88px;*/
  /*  right: 0;*/
  /*  left: 0;*/
  /*}*/
</style>