import axios from 'axios';
export function request(config) {
    //1创建axios实例
    const instance =axios.create({
      baseURL:'http://152.136.185.210:8000/api/n3',//网络请求
      // baseURL:'/api',//本地json数据
      timeout:50000
    });
    //2axios拦截器
     //2.1请求拦截
    instance.interceptors.request.use(config=>{
      //此处拦截了config
      // console.log(config);
      //2.1请求不符合服务器规范在此处做一些修改
      //返回config
      return config;
        }, error => {console.log(error)});
    //2.2响应拦截
      instance.interceptors.response.use(res=>{
        // console.log(res);
        return res
      },err=>{
        console.log(err)
      })
    //3发送真正的网络请求(本身就是一个promise对象)
   return  instance(config);
}
