<template>
  <div class="bottom-bar">
    <div class="check-content">
      <CheckButton
              :is-checked="isSelectedAll"
              @click.native="checkClick"
              class="checks-button"/>
      <span>全选</span>
    </div>
    <div class="check-total">{{totalPrice}}</div>
    <div class="calculate" @click="calcuClick">去计算&nbsp;&nbsp;({{checkedLength}})</div>
  </div>
</template>

<script>
import CheckButton from "@components/content/checkButton/CheckButton";
import {mapGetters} from 'vuex'

export default {
  name: "CartBottomBar",
  components: {
    CheckButton
  },
  computed: {
    ...mapGetters(['cartList']),
    totalPrice() {
      return '￥' + this.cartList.filter((item) => {
        return item.checked
      }).reduce((preValue, item) => {
        return preValue + item.newPrice * item.count
      }, 0).toFixed(2)
    },
    checkedLength() {
      return this.cartList.filter((item) => {
        return item.checked
      }).reduce((preValue, item) => {
        return preValue + item.count
      }, 0)
    },
    isSelectedAll(){
      if (this.cartList.length===0) return false
      // return !(this.cartList.filter(item=>!item.checked).length)
      return !(this.cartList.find(item=>!item.checked))
    }
  },
  created() {
    // console.log(!(this.cartList.filter(item=>!item.checked).length))
  },
  methods:{
    checkClick(){
      if (this.isSelectedAll){
        this.cartList.map(item=>item.checked=false)
      }else {
        this.cartList.map(item=>item.checked=true)
      }
    },
    calcuClick(){
      if (!this.isSelectedAll){
        this.$toast.showToast('未选择任何商品')
      }
    }
  }
}
</script>

<style scoped>
  .bottom-bar {
    display: flex;
    align-items: center;
    height: 40px;
    background-color: #eee;
    padding-left: 10px;
  }

  .check-content {
    display: flex;
    width: 70px;
  }

  .check-content span {
    margin-right: 5px;
    display: flex;
    align-items: center;
  }

  .checks-button {
    text-align: center;
    width: 25px;
    margin-right: 5px;
  }
  .check-total{
    flex: 1;
  }
  .calculate{
    height: 100%;
    width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f00;
    color: #fff;

  }
</style>