import {request} from "./request";
/**
 * @author ss
 * @params
 * @date 2020/7/23
 * @Description: 获取左侧分类数据
 * @return: Promise
*/
export function getCategory() {
  return request({
    url:'/category'
  })
}
export function getMaitCategory(maitKey) {
  return request({
    url:'/subcategory',
    params:{
      maitKey
    }
  })
}
export function getMiniWallCategory(miniWallkey,type) {
  return request({
    url:'/subcategory/detail',
    params:{
      miniWallkey,
      type
    }
  })
}
