<template>
  <div class="goods-item" @click="ItemClick" v-if="goodsItem">
<!--    <img :src="goodsImage" alt="" @load="imgLoaded">-->
<!--    图片懒加载方式-->
    <img v-lazy="goodsItem.img||goodsItem.image||goodsItem.show.img" alt="" @load="imgLoaded">
    <div>
      <p class="text-ellipsis"> {{goodsItem.title}}</p>
      <div class="goods-item-detail">
        <span class="price">{{goodsItem.price}}</span>
        <img src="@assets/img/common/collect.svg" class="col-img">
        <span class="collect">{{goodsItem.cfav}}</span>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "GoodsListItem",
  props: {
    goodsItem: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  mounted() {

  },
  computed: {
  },
  methods: {

    imgLoaded() {
      this.$bus.$emit('ItemLoaded')
      // //通过判断当前路由地址，选择触发事件
      // if (this.$route.path.indexOf('/home') !== -1) {
      //   this.$bus.$emit('homeItemLoaded')
      // } else if (this.$route.path.indexOf('/detail') !== -1) {
      //   this.$bus.$emit('detailItemLoad')
      // }
    },
    ItemClick() {
      this.$router.push('/detail/' + this.goodsItem.iid)
    }
  }
}
</script>

