  .goods-item {
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .goods-item > img {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 5px;
  }

  .goods-item > div {
    width: 100%;
    font-size: 12px;
  }

  .text-ellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 5px;
  }

  .goods-item-detail {
    text-align: center;
    display: flex;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
    margin-bottom: 5px;
  }

  .goods-item-detail span {
    display: inline-block;
