<template>
  <div class="tab-bar-item" @click="itemClick">
    <div v-if="!isActive">
      <slot name="icon" ></slot>
    </div>
    <div v-else>
      <slot name="icon-active" ></slot>
    </div>
    <div :style="activeStyle">
      <slot name="text" ></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabbarItem",
  props:{
    path:String,
    activeColor:{
      type:String,
      default:'red'
    }
  },
  data(){
    return{
      // isActive:true
    }
  },
  computed:{
    isActive(){
      return this.$route.path.indexOf(this.path)!==-1;
    },
    activeStyle(){
      return this.isActive?{color:this.activeColor}:{};
    }

  },
  methods:{
    itemClick(){
        this.$router.push(this.path)
            //解决双击时候的报错
            .catch(()=>{});
      }
  }
}
</script>

<style scoped>
  .tab-bar-item{
    flex: 1;
    height: 49px;
    display: flex;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
    flex-direction: column;
    background-color: #f6f6f6;
    font-size: 14px;
  }
  .tab-bar-item img{
    width:24px;
    height: 24px;
    margin-top: 3px;
  }
</style>