<template>
  <div class="detail">
    <detailNavBar
            ref="nav"
            @titleClick="titleClick"/>
    <Scroll class="content"
            ref="scroll"
            :probe-type="3"
            @scroll="contentScroll">
      <detailSwipper :top-images="topImages"/>
      <DetailBaseInfo :goods="goods"/>
      <DetailShopInfo :shop="shop"/>
      <DetailGoodsInfo
              @imageLoad="imgLoad"
              :detailInfo="detailInfo"/>
      <detailParamsInfo :item-params="itemParams" ref="params"/>
      <detailCommentInfo :commentInfo="commentInfo" ref="comment"/>
      <GoodsList :goods="recommend" ref="recommend"/>
    </Scroll>
    <backTop @click.native="backClick" v-show="isShowBackTop" ></backTop>
    <detailBottomBar @addToCart="addToCart"/>
<!--    <Toast-->
<!--            :show="show"-->
<!--            :message="message"/>-->
  </div>
</template>

<script>
import detailNavBar from "./chidrenComps/detailNavBar";
import detailSwipper from "./chidrenComps/detailSwipper";
import DetailBaseInfo from "./chidrenComps/DetailBaseInfo";
import DetailShopInfo from "./chidrenComps/DetailShopInfo";
import DetailGoodsInfo from "./chidrenComps/DetailGoodsInfo";
import detailParamsInfo from "./chidrenComps/detailParamsInfo";
import detailCommentInfo from "./chidrenComps/detailCommentInfo";
import detailBottomBar from "./chidrenComps/detailBottomBar";
import GoodsList from "@components/content/goods/GoodsList";

import {getDetail, getRecommend, Goods, Shop} from "@network/detail";

import Scroll from "@components/common/scroll/Scroll";
//方法
import {debounce} from '@common/utils';
//mixin
import {itemListenerMixin,backToMixin} from '@common/mixin'
//Action
import {mapActions} from 'vuex'
//toast
// import Toast from "@components/common/toast/Toast";
export default {
  name: "Detail",
  data() {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      itemParams: {},
      commentInfo: {},
      recommend: [],
      itemImgListener: null,
      themTopYs: [],
      getThemeTopY: null,
      currentIndex: 0,
      // message:'',
      // show:false
    }
  },
  mixins: [itemListenerMixin,backToMixin],
  components: {
    detailNavBar,
    detailSwipper,
    DetailBaseInfo,
    DetailShopInfo,
    Scroll,
    DetailGoodsInfo,
    detailParamsInfo,
    detailCommentInfo,
    GoodsList,
    detailBottomBar,
    // Toast
  },
  methods: {
    ...mapActions(['addCart']),
    imgLoad() {
      this.$refs.scroll.refresh();
      //图片加载完成获取offsetTop值，只获取一次
      this.getThemeTopY();
    },
    titleClick(index) {
      this.$refs.scroll.scrollTo(0, -this.themTopYs[index], 100)
    },
    //监听滚动事件，对应tab高亮
    contentScroll(position) {
      // console.log(position)
      //获取Y值
      const positionY = -position.y;
      for (let i in this.themTopYs) {
        //此种方式取出来的i值为str类型
        i *= 1;
        if (this.currentIndex !== i && (positionY >= this.themTopYs[i] && positionY < this.themTopYs[i + 1])) {
          this.currentIndex = i;
          this.$refs.nav.currentIndex = this.currentIndex;
          console.log(this.currentIndex)
        }
        // if (i < this.themTopYs.length - 1) {
        //   if (positionY >= this.themTopYs[i] && positionY < this.themTopYs[i + 1] && this.currentIndex !== i) {
        //     this.currentIndex = i;
        //     this.$refs.nav.currentIndex=this.currentIndex
        //     console.log(this.currentIndex)
        //   }
        // } else {
        //   if (positionY >= this.themTopYs[i] && this.currentIndex !== i) {
        //     this.currentIndex = i;
        //     this.$refs.nav.currentIndex=this.currentIndex
        //     console.log(this.currentIndex)
        //   }
        // }

      }
      console.log(this.isShowBackTop)
      console.log(positionY)
      this.isShowBackTop = positionY > 1000
      console.log(this.isShowBackTop)
    },
    //购物车
    addToCart(){
      //1 获取购物车需要展示的信息
      const product={};
      product.title=this.goods.title
      product.desc=this.goods.desc
      product.newPrice=this.goods.realPrice
      product.image=this.topImages[0]
      product.iid=this.iid
      console.log(product)
      //2.将商品添加到购物车里面
      //2.1vuex action可以返回一个promise对象
      //2.2Action方法可以映射
      // this.$store.commit('addCart',product)
      this.addCart(product).then(res=>{
        console.log(res)
        this.$toast.showToast(res)
        // this.message=res;
        // this.show=true;
        // setTimeout(()=>{
        //   this.show=false
        // },1000)
      })
      // this.$store.dispatch('addCart',product).then(res=>{
      //   console.log(res)
      // })
    }
  },
  created() {
    this.iid = this.$route.params.iid;
    //获取轮播图
    getDetail(this.iid).then(res => {
      this.topImages = res.data.result.itemInfo.topImages;
      // console.log(res)
      //获取商品信息
      let data = res.data.result
      this.goods = new Goods(data.itemInfo, data.columns, data.shopInfo.services)
      //获取店铺信息
      this.shop = new Shop(data.shopInfo)
      //获取商品详情数据
      this.detailInfo = data.detailInfo
      //取出参数信息
      this.itemParams = data.itemParams
      //评论
      if (data.rate.cRate) {
        this.commentInfo = data.rate.list[0]
      }
      // console.log(this.commentInfo)
    })
    //获取推荐
    getRecommend().then(res => {
      this.recommend = res.data.data.list;
      // console.log(this.recommend instanceof Array)
    })
    //给getThemeTopY赋值
    this.getThemeTopY = debounce(() => {
      this.themTopYs = []
      this.themTopYs.push(0)
      this.themTopYs.push(this.$refs.params.$el.offsetTop)
      this.themTopYs.push(this.$refs.comment.$el.offsetTop)
      this.themTopYs.push(this.$refs.recommend.$el.offsetTop)
      this.themTopYs.push(Number.MAX_VALUE);
      // console.log(this.themTopYs)
    })
    // this.$nextTick(()=>{
    //   //根据最新的数据DOM是已经被渲染出来的
    //   //但是图片依旧没有加载完成
    //   //offsetTop的问题一般都是因为图片的问题
    //   this.themTopYs=[]
    //   this.themTopYs.push(0)
    //   this.themTopYs.push(this.$refs.params.$el.offsetTop)
    //   this.themTopYs.push(this.$refs.comment.$el.offsetTop)
    //   this.themTopYs.push(this.$refs.recommend.$el.offsetTop)
    //   console.log(this.themTopYs)
    // })
  },

  mounted() {
    //

  },
  updated() {
    // this.themTopYs = []
    // this.themTopYs.push(0)
    // this.themTopYs.push(this.$refs.params.$el.offsetTop)
    // this.themTopYs.push(this.$refs.comment.$el.offsetTop)
    // this.themTopYs.push(this.$refs.recommend.$el.offsetTop)
    // console.log(this.themTopYs)
  },
  //该页面未做缓存，离开时会销毁
  destroyed() {
    //取消全局事件监听
    console.log('destroyed')
    this.$bus.$off('ItemLoaded', this.itemImgListener);
  },
}
</script>

<style scoped>
  .detail {
    position: relative;
    z-index: 9999;
    background-color: #fff;
    height: 100vh;
  }

  .content {
    height: calc(100% - 93px);
    overflow: hidden;
  }
</style>