<template>
  <div class="category">
    <Navbar class="category-nav">
      <template #center>
        商品分类
      </template>
    </Navbar>
    <Scroll class="category-main"

            ref="scrollRight">
      <ul class="category-main-left">
        <li v-for="(item,index) in category"
            :class="{active:currentIndex===index}"
            @click="isActive(index)"
            class="main-left-item"
            :key="item.title">
          {{item.title}}
        </li>
      </ul>
    </Scroll>
    <Scroll class="category-main-right"
            :probe-type="3"
            :pull-up-load="true"
            @scroll="scrollContent"
            ref="scroll">
      <SubCategoryHd :mait-data="MaitData"/>
      <TabControl :title="title" @tabClick="changeCategory"/>
      <GoodsList :goods="goods[currentType].list"/>
    </Scroll>
    <backTop @click.native="backClick" v-show="isShowBackTop"></backTop>

  </div>
</template>

<script>
import Scroll from "@components/common/scroll/Scroll";
import Navbar from "@components/common/navbar/Navbar";
import TabControl from "@components/content/tabControl/TabControl";

import SubCategoryHd from "./childrenComps/SubCategoryHd";
import GoodsList from "@components/content/goods/GoodsList";
import {backToMixin} from '@common/mixin';
import {getCategory, getMaitCategory,getMiniWallCategory} from "@network/categroy";

export default {
  name: "Category",
  data() {
    return {
      category: [],
      currentIndex: 0,
      maitKey: null,
      miniWallkey:null,
      MaitData: [],
      title:['综合','新品','销量'],
      goods: {
        'pop': {list: []},
        'new': {list: []},
        'sell': {list: []}
      },
      currentType: 'pop',
    }
  },
  mixins:[backToMixin],
  components: {
    Scroll,
    Navbar,
    SubCategoryHd,
    GoodsList,
    TabControl
  },
  created() {
    //获取左侧列表
    getCategory().then(res => {
      this.category = res.data.data.category.list;
      this.isActive(0);
    });


  },
  mounted() {
    // tabClick()
  },

  methods: {
    //这里获取数据然后传入子组件
    isActive(index) {
      this.currentIndex = index;
      this.maitKey = this.category[index].maitKey
      this.miniWallkey = this.category[index].miniWallkey
      //获取右侧分类头部数据
      if (this.maitKey) {
        getMaitCategory(this.maitKey).then(res => {
          // console.log(res)
          this.MaitData = res.data.data.list
        });
      }
      //获取右侧分类底部信息
      if (this.miniWallkey){
        let typeList=Object.keys(this.goods);
        for(let item of typeList){
          getMiniWallCategory(this.miniWallkey,item).then(res=>{
            // console.log(res)
            this.goods[item].list.push(...res.data);
            //完成下拉更多
            // this.$refs.scroll.finishPullUp();
          })
        }
      }
    },
    //切换底部分类
    changeCategory(index){
      let typeList=Object.keys(this.goods);
      // console.log(typeList)
      this.currentType=typeList[index]
      console.log(this.currentType)
    },
    //监听首页滚动
    scrollContent(position) {
      //判断backToTop是否显示
      this.isShowBackTop = -position.y > 1000;
      // //tabControl是否吸顶
      // this.isFixed = -position.y > this.tabOffsetTop
    }
  }
}
</script>

<style scoped>
  .category {
    height: 100vh;
  }

  .category-nav {
    background-color: var(--color-tint);
    color: #fff;
    font-size: 16px;
  }

  .main-left-item {
    width: 100%;
    background-color: #eee;
    height: 40px;
    display: flex;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
    width: 100px;
  }

  .main-left-item.active {
    background-color: #fff;
    color: var(--color-tint);
    border-left: 3px solid var(--color-tint);
  }

  .category-main {
    position: absolute;
    left: 0;
    top: 44px;
    width: 100px;
    height: calc(100% - 93px);
    overflow: hidden;
  }

  .category-main-right {
    width: 275px;
    position: absolute;
    top: 44px;
    right: 0;
    height: calc(100% - 93px);
    overflow: hidden;
  }
</style>