<template>
  <Navbar class="">
      <template #left class="left" >
        <div @click="backTo">
          <img src="@assets/img/common/back.svg" alt="">
        </div>
      </template>
    <template #center>
      <ul class="detail-nav">
        <li v-for="(item,index) in titles"
            :class="{active:currentIndex===index}"
            @click="titleClick(index)"
            class="detail-nav-item" >{{item}}</li>
      </ul>
    </template>
  </Navbar>
</template>

<script>
import Navbar from "@components/common/navbar/Navbar";



export default {
  name: "detailNavBar",
  data(){
    return{
      titles:['商品','参数','评论','推荐'],
      currentIndex:0
    }
  },
  components:{
    Navbar
  },
  methods:{
    backTo(){
      this.$router.go(-1)
    },
    titleClick(index){
     this.currentIndex= index;
     this.$emit('titleClick',index)
    }
  },
  created() {

  }
}
</script>

<style scoped>
  .detail-nav{
    display: flex;
    font-size: 14px;
  }
.detail-nav-item{
  flex: 1;
  list-style: none;
}
.nav-bar-left img{
  margin-top: 10px;
  }
  .active{
    color: var(--color-high-text);
  }
</style>