<template>
  <Swiper>
    <SwiperItem v-for="item in banner" >
      <a :href="item.link">
        <img :src="item.image" alt="" @load="imageLoad">
      </a>
    </SwiperItem>
  </Swiper>
</template>

<script>
import {Swiper,SwiperItem} from "@components/common/swiper"
export default {
  name: "Homeswipper",
  props:{
    banner:{
      default(){
        return []
      }
    }
  },
  data(){
    return {
      isLoaded :false
    }
  },
  components:{
    Swiper,
    SwiperItem
  },
  methods:{
    imageLoad(){
      if (!this.isLoaded){
        this.$emit('swiperImageLoad');
        this.isLoaded=!this.isLoaded
      }
    }
  }

}
</script>

