<template>
  <div class="profile-count">
    <div class="profile-count-item">
      <div class="number"><span>{{countSum}}</span>元</div>
      <div class="text">我的余额</div>
    </div>
    <div class="profile-count-item">
      <div class="number"><span>{{sale}}</span>个</div>
      <div class="text">我的优惠</div>
    </div>
    <div class="profile-count-item">
      <div class="number"><span>{{integral}}</span>分</div>
      <div class="text">我的积分</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProfileCount",
  data(){
    return{
      'count_sum':0,
      'sale':0,
      'integral':0
    }
  },
  computed:{
    countSum(){
      return (this.count_sum).toFixed(2)
    }
  }
}
</script>

<style scoped>
  .profile-count{
    display: flex;
    justify-content: space-around;
    -ms-align-items: center;
    align-items: center;
    padding: 20px 0;
  }
  .profile-count-item{
    display: flex;
    flex: 1;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .profile-count-item:nth-child(2){
    border-left: 1px solid #F1F0F2;
    border-right: 1px solid #F1F0F2;
  }
  .number span {
    color: var(--color-tint);
    font-size: 20px;
    font-weight: 700;
  }
  .text{
    margin-top: 5px;
    font-size: 14px;
  }
</style>