import {request} from "./request";
export function getHomeMultidata() {
  return request({
    url:'/home/multidata'  //网络请求
    // url:'/home.json'//本地json数据
  })
}
export function getHomeGoods(type,page) {
 return request({
   url: 'home/data',
   params:{
     type,
     page
   }
 })


}
