<template>
  <div class="tab-control">
      <div v-for="(item,index) in title" class="tab-control-item"
           :class="{active:index===currentIndex}"
           @click="itemClick(index)"
      >
        <span>{{item}}</span>
      </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  props:{
    title:{
      type:Array,
      default(){
        return []
      }
    }
  },
  data(){
    return{
      currentIndex:0
    }
  },
  methods:{
    itemClick(index){
      this.currentIndex=index;
      this.$emit('tabClick',index)
    }
  }
}
</script>

<style scoped>
.tab-control{
  width: 100%;
  padding: 10px 30px;
  display: flex;
  background-color: #fff;
}
  .tab-control-item{
    flex: 1;
    text-align: center;
    font-size: 15px;
  }
  .active{
    color: var(--color-high-text);
  }
   span {
    display: inline-block;
    border-bottom: 2px solid #fff;
    padding-bottom: 10px;
  }
  .active span {
    border-bottom: 2px solid var(--color-high-text);
  }
</style>