<template>
  <div class="toast" v-show="show">
    <div>{{message}}</div>
  </div>
</template>

<script>
export default {
  name: "Toast",
  // props:{
  //   message:{
  //     type:String,
  //     default:''
  //   },
  //   show:{
  //     type:Boolean,
  //     default: false
  //   }
  // },
  data(){
    return{
      message:'',
      show:false
    }
  },
  methods:{
    showToast(message,duration=1000){
      this.show=true;
      this.message=message;
      setTimeout(()=>{
        this.show=false;
        this.message='';
      },duration)
    }
  }
}
</script>

<style scoped>
.toast{
  position: fixed;
  top:50%;
  left: 50%;
  padding: 10px;
  color: #fff;
  background-color: rgba(0,0,0,.8);
  transform: translate(-50%,-50%);
  z-index: 9999;
  border-radius: 5px;
}
</style>